import React from "react";

const Description = (props) => {
  return (
    <React.Fragment>
      <h1 className="titlu">{props.photoData.title}</h1>
      {props.photoData.media_type === "image" ? (
        <div className="parinte-poza">
          <img className="poza" src={props.photoData.url} alt="universephoto" />
        </div>
      ) : (
        <iframe
          title="space-video"
          source={props.photoData.url}
          frameBorder="0"
          gesture="media"
          allow="encrypted-media"
          allowFullScreen
          className="photo"
        />
      )}
      <div>
        <p className = "data">{props.photoData.date}</p>
        <p className = "descriere">{props.photoData.explanation}</p>
      </div>
    </React.Fragment>
  );
};

export default Description;
