import React from 'react'
import {Link} from 'react-router-dom'

const  Home = (props) => {
    return (
        <div>
            <h1 className = "titlu2">Picture/Video of the day / NASA APOD API</h1>
            <Link className = "link2" to = '/nasaphoto' >Read more</Link>
            <div className = "parinte-poza2"><img className = "poza2" src = {props.photoData.url} alt = 'nasa' /></div>
        </div>
    )
}

export default Home
